// import * as core from "../core";
import router from "../router";
import _ from "lodash";

export default (instance) => {
  const item = instance.data.item;
  let variantIdx = 0;

  const isOutOfStock =
    item.structuredContent.variants[variantIdx].qtyInStock === 0;
  const isActivateLiveShop = router.element.is(".activate-live-shop");
  const getAttributes = () => {
    const attrs = [];

    return item.excerpt || "";

    for (const i in item.structuredContent.variants[variantIdx].attributes) {
      if (
        item.structuredContent.variants[variantIdx].attributes.hasOwnProperty(i)
      ) {
        attrs.push(
          `<h6>${item.structuredContent.variants[variantIdx].attributes[i]}</h6>`
        );
      }
    }

    return attrs.join("");
  };

  const renderSelects = (item) => {
    return `
    <div class="variant-select sqs-row">
      ${item.variantOptionOrdering
        .map((optionName) => {
          let options = item.variants.map((v) => v.attributes[optionName]);
          options = _.uniq(options);
          return `
          <div class="variant-group">
              <div class="h6 variant-label">
                ${optionName}
              </div>
              <div class="variant-options">
              ${options
                .map((opt, i) => {
                  return `
                    <div class="radio-group">
                        <input ${
                          i === 0 ? "checked" : ""
                        } type="radio" id="${opt}" name="${optionName}" value="${opt}">
                        <label class="h6" for="${opt}">${opt}</label><br>
                    </div>
                `;
                })
                .join("")}
              </div>
          </div>
        `;
        })
        .join("")}
    </div>`;
    // <select name="${optionName}" class="dropdown h6">
    //   ${_.uniq(options).map((opt) => {
    //     return `<option value="${opt}">${opt}</option>`;
    //   })}
    // </select>
  };

  return `
        <div class="p1 ${isOutOfStock ? `is-out-of-stock` : ``}">
            <div class="text-col">
                <div class="p1__title">
                    <h4>${item.title}</h4>
                </div>
                ${
                  item.structuredContent.variants.length
                    ? renderSelects(item.structuredContent)
                    : ``
                }
                ${item.structuredContent.variants
                  .map((variant, i) => {
                    return `
                        <div class="comm-purchase p1__button sqs-row ${
                          i == 0 ? "" : "hidden"
                        }" data-variant-id="${variant.id}">
                            ${
                              isActivateLiveShop
                                ? `<div class="_button js-button_" data-item-id="${variant.id}" data-sku="${variant.sku}" data-block-json="">${item.structuredContent.customAddButtonText}</div>`
                                : `<div class="_button js-button_" style="pointer-events:none;" data-block-json="">Coming soon</div>`
                            }
                            <div class="sqs-block-content">
                                ${
                                  isActivateLiveShop
                                    ? `<p class="h6">$${variant.priceMoney.value}</p>`
                                    : `<p></p>`
                                }
                            </div>
                        </div>
                        `;
                  })
                  .join("")}

                ${
                  isActivateLiveShop
                    ? `
                    <div class="p1__attributes">
                        ${getAttributes()}
                    </div>
                `
                    : ``
                }
            </div>
            <div class="image-col">
                <div class="media js-media">
                    <div class="media__wrap">
                        <img class="media__node image js-lazy-image" data-img-src="${
                          item.assetUrl
                        }" data-variants="${
    item.systemDataVariants
  }" data-original-size="${item.originalSize}" />
                    </div>
                </div>
            </div>
        </div>
        <article data-item-id="${item.id}" class="story story--shop js-story">
            <div class="story__blocks">
                ${item.body}
            </div>
        </div>
    `;
};
